"use client"
import { motion, useScroll, useTransform } from "framer-motion"
import { useRef } from "react"
import Image from "next/image"

export default function WhatIsWorkly() {
  const ref = useRef<HTMLDivElement>(null)
  const { scrollYProgress } = useScroll({
    target: ref,
    offset: ["start end", "end start"],
  })

  const lines = [
    "Workly helps people find reliable local experts for anything from home repairs to everyday tasks.",
    "As a service provider, you can earn on your own schedule, doing the work you’re skilled at —",
    "while helping someone in your community get things done faster.",
  ]

  return (
    <section id="what-is-workly" ref={ref} className="relative w-full bg-white overflow-hidden">
      {/* ✅ Subtle floating gradient accent */}
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 0.15 }}
        transition={{ duration: 1.5 }}
        className="absolute top-1/3 left-1/4 w-[400px] h-[400px] bg-sky-400/30 blur-[120px] rounded-full pointer-events-none"
      />

      <div className="relative flex flex-col md:flex-row items-stretch">
        {/* ✅ Left Side - Image */}
        <motion.div
          initial={{ opacity: 0, x: -80 }}
          whileInView={{ opacity: 1, x: 0 }}
          transition={{ duration: 0.8, ease: "easeOut" }}
          viewport={{ once: true }}
          className="relative w-full md:w-1/2 h-72 md:h-[500px] overflow-hidden"
        >
          <motion.div
            whileHover={{ scale: 1.05 }}
            transition={{ duration: 0.6, ease: "easeOut" }}
            className="h-full w-full relative"
          >
            <Image
              src="/workly-cleaning.png"
              alt="What is Workly"
              fill
              priority
              className="object-cover object-center transition-transform duration-700 ease-out hover:scale-105"
            />
            <div className="absolute inset-0 bg-gradient-to-tr from-white/40 via-transparent to-transparent" />
          </motion.div>
        </motion.div>

        {/* ✅ Right Side - Text */}
        <motion.div
          initial={{ opacity: 0, x: 80 }}
          whileInView={{ opacity: 1, x: 0 }}
          transition={{ duration: 0.8, delay: 0.2, ease: "easeOut" }}
          viewport={{ once: true }}
          className="relative text-xs w-full md:w-1/2 flex items-center justify-center bg-[#FFF8F1] px-6 md:px- py-14 md:py-0"
        >
          <div className="max-w-lg text-center md:text-left relative z-10">
            <motion.h2
              initial={{ opacity: 0, y: 20 }}
              whileInView={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.8, delay: 0.3 }}
              viewport={{ once: true }}
              className="text-3xl md:text-4xl font-bold text-gray-900 mb-6"
            >
              What is{" "}
              <span className="text-transparent bg-clip-text bg-gradient-to-r from-sky-600 to-blue-700">
                Workly
              </span>
              ?
            </motion.h2>

            {/* ✅ Animated Lines - normal weight + color #404040 */}
            <div className="space-y-1">
              {lines.map((line, i) => {
                const start = i * 0.2
                const end = start + 0.1
                const color = useTransform(scrollYProgress, [start, end], ["#b0b0b0", "#404040"])

                return (
                  <motion.p
                    key={i}
                    style={{ color }}
                    className="text-base md:text-lg leading-relaxed font-normal"
                  >
                    {line}
                  </motion.p>
                )
              })}
            </div>

            {/* ✅ Subtle divider line */}
            <motion.div
              initial={{ scaleX: 0 }}
              whileInView={{ scaleX: 1 }}
              transition={{ duration: 0.8, delay: 0.6, ease: "easeOut" }}
              viewport={{ once: true }}
              className="mt-6 w-24 h-[3px] bg-gradient-to-r from-sky-500 to-blue-600 rounded-full origin-left"
            />
          </div>
        </motion.div>
      </div>
    </section>
  )
}
